import { Component, Host, h, Prop } from '@stencil/core';
import { CalloutBoxVariation } from './callout-box.types';
import { TextColor, TextSize } from '../../atoms/text/text.types';

/**
 * Callout box component.
 */
@Component({
  tag: 'bs-callout-box',
  styleUrl: 'callout-box.scss',
  shadow: true,
})
export class BsCalloutBox {
  /**
   *
   * Callout box content text
   */
  @Prop() text: string;

  /**
   *
   * Callout box variation
   */
  @Prop() variation: string = CalloutBoxVariation.Primary;

  render() {
    return (
      <Host>
        <div class={`callout-box callout-box--${this.variation}`}>
          <bs-text textColor={TextColor.White} size={TextSize.Bold} text={this.text} />
        </div>
      </Host>
    );
  }
}
